import React, { useState } from 'react'
import {
  Avatar,
  Card,
  ListItem,
  Icon,
  Overlay,
  Button,
} from 'react-native-elements'
import { Text, Linking, View, Modal } from 'react-native'
import { H3 } from '@expo/html-elements'
import { EXPLORER_MAP } from 'staker-freenodes'

import { CHAIN_MAP } from 'helpers/constants'
import { checkAddr } from 'helpers/address'
import commonStyle from 'helpers/commonStyle'
import useLocalStorage from 'hooks/useLocalStorage'

const AddrTx = ({ term, chainId }) => {
  const [visible, setVisible] = useState(false)
  const [explorer, setExplorer] = useLocalStorage(
    `${chainId}_explorer`,
    EXPLORER_MAP[chainId] && Object.keys(EXPLORER_MAP[chainId])[0]
  )
  const chainExplorer = EXPLORER_MAP[chainId]
  if (!chainExplorer) return <H3>Explorer Not found</H3>

  const [isAddress, isTx] = checkAddr(term)
  // const explorers = Object.keys(chainExplorer)

  const toggleOverlay = () => setVisible(!visible)

  const SelectExplorerModal = () => (
    <Modal
      animationType='slide'
      transparent
      visible={visible}
      onRequestClose={toggleOverlay}
    >
      <Card>
        <Card.Title>Pick the {CHAIN_MAP[chainId]} Explorer </Card.Title>
        <View style={[commonStyle.row, commonStyle.alignSelfCenter]}>
          {Object.keys(chainExplorer).map((entry) => {
            const { name } = chainExplorer[entry]
            console.log(entry, name)
            return (
              <Button
                key={entry}
                title={`üè† ${name}`}
                type={entry === explorer ? 'solid' : 'outline'}
                onPress={() => {
                  toggleOverlay()
                  setExplorer(entry)
                }}
                buttonStyle={{ marginRight: 4 }}
              />
            )
          })}
        </View>
        <Button
          title='Close'
          icon={{
            name: 'cross',
            type: 'entypo',
            color: '#2089dc',
          }}
          iconRight
          onPress={toggleOverlay}
          type='outline'
          style={[commonStyle.alignSelfCenter, { marginTop: 8 }]}
        />
      </Card>
    </Modal>
  )

  const explorerUrl = () => {
    if (isAddress) return chainExplorer[explorer].address(term)
    if (isTx) return chainExplorer[explorer].tx(term)
    return ''
  }

  if (explorerUrl) {
    return (
      <View style={[commonStyle.row, commonStyle.alignSelfCenter]}>
        <Icon
          name='magnifying-glass'
          type={commonStyle.selectIconType}
          // color={commonStyle.selectIconColor}
          size={24}
          onPress={() => Linking.openURL(explorerUrl())}
          buttonStyle={commonStyle.buttonMarginRight}
        />
        {Object.keys(chainExplorer).length > 1 && (
          <>
            <Icon
              name={commonStyle.selectIconName}
              type={commonStyle.selectIconType}
              color={commonStyle.selectIconColor}
              size={24}
              onPress={toggleOverlay}
            />
            <Overlay ModalComponent={SelectExplorerModal} />
          </>
        )}
      </View>
    )
  }
  // if (isAddress) {
  //   return (
  //     <>
  //       {explorers &&
  //         explorers.map((key) => (
  //           <ListItem key={chainExplorer[key].name}>
  //             <Avatar rounded title='üîé' />
  //             <ListItem.Content>
  //               <ListItem.Title>
  //                 <Text
  //                   onPress={() =>
  //                     Linking.openURL(chainExplorer[key].address(addr))
  //                   }
  //                 >
  //                   {chainExplorer[key].name}
  //                 </Text>
  //               </ListItem.Title>
  //             </ListItem.Content>
  //           </ListItem>
  //         ))}
  //     </>
  //   )
  // }
  // if (isTx) {
  //   return (
  //     <>
  //       {explorers &&
  //         explorers.map((key) => (
  //           <ListItem key={chainExplorer[key].name}>
  //             <Avatar rounded title='üîé' />
  //             <ListItem.Content>
  //               <ListItem.Title>
  //                 <Text
  //                   onPress={() => Linking.openURL(chainExplorer[key].tx(addr))}
  //                 >
  //                   {chainExplorer[key].name}
  //                 </Text>
  //               </ListItem.Title>
  //             </ListItem.Content>
  //           </ListItem>
  //         ))}
  //     </>
  //   )
  // }
  return <H3>Invalid Address</H3>
}

export const Explorers = ({ chainId, term }) => {
  return (
    <>
      {/* <Card>
      <Card.Title>üîé {CHAIN_MAP[chainId]} Chain Explorer</Card.Title> */}
      <AddrTx term={term} chainId={chainId} />
      {/* </Card> */}
    </>
  )
}

export default Explorers
