import React from 'react'
import { version as nodesVersion } from 'staker-freenodes/package.json'
import { version as contractsVersion } from 'staker-contracts/package.json'

import Root from 'routes/Root'
import { version as webVersion } from './package.json'

const label = 'versions'
const App = () => {
  /* eslint-disable */
  console.log(
    'commandline version is available, check https://www.npmjs.com/package/staker'
  )
  console.group(label);
  console.log(`web v${webVersion}`)
  console.log(`staker-freenodes v${nodesVersion}`)
  console.log(`staker-contracts v${contractsVersion}`)
  console.groupEnd(label);
  /* eslint-enable */
  return <Root />
}
export default App
