import React from 'react'
import { StyleSheet, View, ScrollView, useWindowDimensions } from 'react-native'
import { H1, Header, Main, P, Footer } from '@expo/html-elements'
import { CHAIN_ETHEREUM, GASNOW } from 'staker-freenodes'

import { SearchBar } from 'components/SearchBar'
import { Balance } from 'components/Balance'
import { GasFee } from 'components/GasFee'
import { SupportUs } from 'components/SupportUs'
import { DefiBalances } from 'components/DefiBalances'
import useLocalStorage from 'hooks/useLocalStorage'
import { SUPPORT_CHAINS } from 'helpers/constants'
import { i18n } from 'helpers/i18n'

const bgColor = 'white'
const styles = StyleSheet.create({
  container: {
    alignItems: 'center',
    backgroundColor: bgColor,
    flex: 1,
    justifyContent: 'center',
  },
})

export default function Root() {
  const [term, onChangeSearchTerm] = useLocalStorage('addr', '')
  const [gasEstimator, setGasEstimator] = useLocalStorage('gas', GASNOW)
  const { width } = useWindowDimensions()
  // TOOD: masonry layout
  const isDesktop = width > 963
  // const [chainIdx, onChangeChainIdx] = useLocalStorage('chainId', 0)
  // const chainId = SUPPORT_CHAINS[chainIdx]
  // const onSearch = () => {
  //   console.log(term, chainId)
  // }

  return (
    <View style={styles.container}>
      <Header style={{ alignItems: 'center' }}>
        <H1>{i18n.en.title}</H1>
        <GasFee gasEstimator={gasEstimator} setGasEstimator={setGasEstimator} />
      </Header>
      <Main>
        <SearchBar
          // chainIdx={chainIdx}
          // onChangeChainIdx={onChangeChainIdx}
          term={term}
          onChangeSearchTerm={onChangeSearchTerm}
          // onSearch={onSearch}
        />
        <ScrollView horizontal={isDesktop}>
          <View
            style={
              isDesktop
                ? { flexDirection: 'row', width: width * 0.8 }
                : { flexDirection: 'column' }
            }
          >
            {SUPPORT_CHAINS.map((chainId, idx) => (
              <Balance key={chainId} chainId={chainId} term={term} />
            ))}
            <DefiBalances chainId={CHAIN_ETHEREUM} term={term} />
          </View>
        </ScrollView>
        <SupportUs />
      </Main>
      <Footer>
        <P>{i18n.en.credit}</P>
      </Footer>
      {/* <StatusBar style="auto" /> */}
    </View>
  )
}
