// ==== zkSync JSON RPC ===
// https://zksync.io/api/v0.1.html

let idx = 1

export const rpcTokens = JSON.stringify({
  jsonrpc: '2.0',
  id: idx++,
  method: 'tokens',
  params: [],
})

export const rpcZkSyncBalance = (address) =>
  JSON.stringify({
    jsonrpc: '2.0',
    id: idx++,
    method: 'account_info',
    params: [address],
  })
