import React, { useEffect, useState } from 'react'
import { Card, ListItem, Text } from 'react-native-elements'

import { CHAIN_MAP } from 'helpers/constants'
import { jsonRpcFetch } from 'helpers/jsonRpc'
import { Explorers } from 'components/Explorers'

import { rpcTokens, rpcZkSyncBalance } from './rpc'

export const ZkSyncBalance = ({ chainId, term }) => {
  const [balance, setBalance] = useState([])
  const data = []
  useEffect(() => {
    async function fetchZksyncBalance() {
      const addresses = [term]
      const nodeUrl = 'https://api.zksync.io/jsrpc'

      const tokenJson = await jsonRpcFetch(fetch, nodeUrl, rpcTokens)
      const tokenMap = tokenJson.result

      for (let i = 0; i < addresses.length; i++) {
        const json = await jsonRpcFetch(
          fetch,
          nodeUrl,
          rpcZkSyncBalance(addresses[i])
        )
        const balances = json.result?.committed?.balances || {}
        if (Object.keys(balances).length > 0) {
          Object.entries(balances).forEach(([sym, balance]) => {
            data.push({
              // address: addresses[i],
              token: sym,
              balance: (Number(balance) / 10 ** tokenMap[sym].decimals).toFixed(
                4
              ),
              source: '',
            })
          })
        }
      }

      setBalance(data)
    }

    term && fetchZksyncBalance()
  }, [term])

  return (
    <Card>
      <Card.Title>üí∞ {CHAIN_MAP[chainId]} Balance</Card.Title>
      <Explorers chainId={chainId} term={term} />
      <Card.Divider />
      {balance.length > 0 &&
        balance.map((token) => (
          <ListItem key={token.token}>
            <Text>
              {token.token.padEnd(20, ' ‚Äã')}
              {token.balance}
            </Text>
          </ListItem>
        ))}
    </Card>
  )
}

export default ZkSyncBalance
