import React, { useEffect, useState } from 'react'
import { Avatar, Card, ListItem, Text } from 'react-native-elements'
import { getNodeURL, CHAIN_ETHEREUM } from 'staker-freenodes'

import { DeFiSDK } from 'defi-sdk'
import BN from 'bignumber.js'

import { isAddr } from 'helpers/address'
import { jsonStringify } from 'helpers/utils'

function getNormalizedNumber(number, decimals) {
  return new BN(number).dividedBy(new BN(10).pow(decimals))
}

export const DefiBalances = ({ chainId, term }) => {
  const isValidQuery = isAddr(term) && chainId === CHAIN_ETHEREUM
  const [balanceOnProtocls, setBalance] = useState([])
  useEffect(() => {
    async function fetching() {
      const defiSdk = new DeFiSDK(getNodeURL(chainId))
      // TODO: multi selector
      // const protocols = await defiSdk.getProtocolNames()
      // console.log(jsonStringify(protocols))
      const balances = await defiSdk.getProtocolBalances(term, [
        'Aave',
        'Aave V2',
        'Aave V2 ‚Ä¢ Variable Debt',
        'Aave V2 ‚Ä¢ Stable Debt',
        'Aave ‚Ä¢ Staking',
        'Aave ‚Ä¢ Uniswap Market',
        'Uniswap V2',
        'SushiSwap',
        'SushiSwap ‚Ä¢ Staking',
        '1inch Liquidity Protocol',
        '1inch LP ‚Ä¢ Staking',
      ])
      setBalance(balances)
    }
    fetching()
  }, [chainId, term])

  if (!isValidQuery || balanceOnProtocls.length === 0) return null

  return (
    <Card>
      {balanceOnProtocls.map((protocol) => {
        return (
          <>
            <Card.Title key={protocol.metadata.name}>
              {/* <Avatar
                  size='small'
                  source={{
                    uri: protocol.metadata.logo,
                  }}
                /> */}
              {protocol.metadata.name}
            </Card.Title>
            <Card.Divider />
            {protocol.balances.length > 0 &&
              protocol.balances.map((protocolBalances) => {
                // Each adapter could either be an Asset or Debt on the protocol
                return (
                  <>
                    {protocolBalances.balances.map((balance) => {
                      // console.log(jsonStringify(balance))
                      const position = {
                        Token: balance.base.metadata.symbol,
                        Balance: getNormalizedNumber(
                          balance.base.balance.toString(),
                          balance.base.metadata.decimals
                        ).toString(),
                      }

                      // If asset is a derivative then there will be underlying assets
                      if (balance.underlying.length > 0) {
                        const underlying = []
                        balance.underlying.forEach((asset) => {
                          underlying.push({
                            Component: asset.metadata.symbol,
                            Amount: getNormalizedNumber(
                              asset.balance.toString(),
                              asset.metadata.decimals
                            ).toString(),
                          })
                        })
                        position.Underlying = underlying
                      }
                      // console.log('Position:', position)
                      return (
                        <ListItem key={position.Token}>
                          <ListItem.Content>
                            <ListItem.Title>
                              {position.Token} {position.Balance}
                            </ListItem.Title>
                            <ListItem.Subtitle>
                              {position.Underlying.map(
                                (token) => `${token.Component} ${token.Amount}`
                              ).join(' ')}
                            </ListItem.Subtitle>
                          </ListItem.Content>
                        </ListItem>
                      )
                    })}
                  </>
                )
              })}
            <Card.Divider />
          </>
        )
      })}
      {/* <Text>{jsonStringify(balanceOnProtocls)}</Text> */}
    </Card>
  )
}

export default DefiBalances
