import React from 'react'
import {
  CHAIN_ETHEREUM,
  CHAIN_XDAI,
  CHAIN_BSC,
  L2_ZKSYNC,
} from 'staker-freenodes'

import { isAddr } from 'helpers/address'
import { SUPPORT_CHAINS } from 'helpers/constants'
// import { formatAddressBalances, generateBalanceQuery } from './Balance.helper'
import { EthScanBalance } from './EthScanBalance'
import { ZkSyncBalance } from './ZkSyncBalance'
import { Placeholder } from './Placeholder'

export const Balance = ({ chainId, term }) => {
  const isValidQuery = isAddr(term) && SUPPORT_CHAINS.includes(chainId)
  if (!isValidQuery) return null
  // console.log('balance of', chainId)
  switch (chainId) {
    case CHAIN_ETHEREUM:
    case CHAIN_XDAI:
    case CHAIN_BSC:
      return <EthScanBalance chainId={chainId} term={term} />
    case L2_ZKSYNC:
      return <ZkSyncBalance chainId={chainId} term={term} />
    default:
      return <Placeholder />
  }
}

export default Balance
