import React, { useEffect, useState } from 'react'
import { Modal, View } from 'react-native'
import { Icon, Overlay, Card, Button } from 'react-native-elements'
import {
  GAS_ESTIMATOR_ETHEREUM as GAS_ESTIMATOR,
  GASNOW,
  CHAIN_ETHEREUM,
} from 'staker-freenodes'
// import _upperCase from 'lodash/upperCase'
import { H3 } from '@expo/html-elements'

import commonStyle from 'helpers/commonStyle'

export const GasFee = ({
  chainId = CHAIN_ETHEREUM,
  gasEstimator = GASNOW,
  setGasEstimator,
}) => {
  const [gasStat, setGasStat] = useState({})
  const [visible, setVisible] = useState(false)

  useEffect(() => {
    // if (chainId !== CHAIN_ETHEREUM) return
    const estimator = GAS_ESTIMATOR[gasEstimator]
    fetch(estimator.api)
      .then((response) => response.json())
      .then((json) => {
        const data = estimator.processGasData(json)
        setGasStat(data)
      })
  }, [chainId, gasEstimator])

  if (chainId !== CHAIN_ETHEREUM) return null

  const toggleOverlay = () => setVisible(!visible)

  const SelectGasModal = () => (
    <Modal
      animationType='slide'
      transparent
      visible={visible}
      onRequestClose={toggleOverlay}
    >
      <Card>
        <Card.Title>Pick a Gas Estimator </Card.Title>
        <View style={[commonStyle.row, commonStyle.alignSelfCenter]}>
          {Object.keys(GAS_ESTIMATOR).map((estimator) => {
            const { name } = GAS_ESTIMATOR[estimator]
            // console.log(estimator, name)
            return (
              <Button
                key={estimator}
                title={`‚õΩÔ∏è ${name}`}
                type={gasEstimator === estimator ? 'solid' : 'outline'}
                onPress={() => {
                  toggleOverlay()
                  setGasEstimator(estimator)
                }}
                buttonStyle={commonStyle.buttonMarginRight}
              />
            )
          })}
        </View>
        <Button
          title='Close'
          icon={{
            name: 'cross',
            type: 'entypo',
            color: '#2089dc',
          }}
          iconRight
          onPress={toggleOverlay}
          type='outline'
          style={[commonStyle.alignSelfCenter, { marginTop: 8 }]}
        />
      </Card>
    </Modal>
  )

  // return (
  //   <Card>
  //     <Card.Title>
  //       ‚õΩÔ∏è Gas Fee (via {_upperCase(gasStat.source) || ''})
  //     </Card.Title>
  //     <Card.Divider />
  //     <ListItem style={{ alignSelf: 'center' }}>
  //       <Text style={{ fontSize: 32 }}>
  //         üöÄ{gasStat.H} üöò{gasStat.M} üö≤{gasStat.L}
  //       </Text>
  //       {/* <Text style={{ fontSize: 48 }}>üöò {gasStat.M}</Text> */}
  //     </ListItem>
  //   </Card>
  // )
  return (
    <H3>
      üöÄ{gasStat.H} üöò{gasStat.M} üö≤{gasStat.L}{' '}
      <Icon
        name={commonStyle.selectIconName}
        type={commonStyle.selectIconType}
        color={commonStyle.selectIconColor}
        size={commonStyle.selectIconSize}
        onPress={toggleOverlay}
      />
      <Overlay ModalComponent={SelectGasModal} />
    </H3>
  )
}

export default GasFee
